const btn = document.querySelector('.js-move-to')
const catalog = document.querySelector('.catalog')
const SneakMax = document.querySelector('.header__logo')
const hero = document.querySelector('.hero')
const about = document.querySelector('.about')
const quiz = document.querySelector('.quiz')
const team = document.querySelector('.team')
const faq = document.querySelector('.faq')
const contacts = document.querySelector('.contacts')

// * NAVIGATION --------------------------------
const catalogLink = document.querySelector('.nav__link--catalog')
const aboutLink = document.querySelector('.nav__link--about')
const quizLink = document.querySelector('.nav__link--quiz')
const teamLink = document.querySelector('.nav__link--team')
const faqLink = document.querySelector('.nav__link--faq')
const contactsLink = document.querySelector('.nav__link--contacts')



function scrollTo(element) {
	window.scroll({
		left: 0,
		top: element.offsetTop,
		behavior: 'smooth'
	})
}

btn.addEventListener('click', () => {
	scrollTo(catalog)
})

SneakMax.addEventListener('click', () => {
	scrollTo(hero)
})

// * Smooth scroll navigation ------------------------------------

catalogLink.addEventListener('click', () => {
	scrollTo(catalog)
})

aboutLink.addEventListener('click', () => {
	scrollTo(about)
})

quizLink.addEventListener('click', () => {
	scrollTo(quiz)
})

teamLink.addEventListener('click', () => {
	scrollTo(team)
})

faqLink.addEventListener('click', () => {
	scrollTo(faq)
})

contactsLink.addEventListener('click', () => {
	scrollTo(contacts)
})




